 .titler{
    text-align: center;
  }
  
  .card{
    position: relative;
    width: 350px;
    height: 400px;
    margin-bottom: 10px;
    margin-left: 30px;
    margin-top: 20px;
    background: #fff;
    box-shadow: 0 3px 10px rgba(0,0,0, .5);
    border-radius: 15px;
  }

  
  .card .face{
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .card .face.face1{
    box-sizing: border-box;
    padding: 20px;
  }
  
  .card .face.face1 h2{
    margin: 0;
    padding: 0;
  }
  
  .card .face.face1 .content{
    font-size:1.2em;
    margin:0;
    padding:0 0 1em 0;
    font-weight:500;
  }
  
  .card .face.face2{
    background: #111;
    transition: 0.5s;
  }
  
  .card:nth-child(1) .face.face2{
    background: linear-gradient(45deg,#eaeaea,white);
    border-radius: 15px;
  }
  
  .card:nth-child(2) .face.face2{
    background: linear-gradient(45deg,white,#8943F4);
    border-radius: 15px;
  }
  
  .card:nth-child(3) .face.face2{
    background: linear-gradient(45deg, #8943F4,white);
    border-radius: 15px;
  }
  
  .card:hover .face.face2{
    height: 60px;
    border-radius: 0 0 15px 15px;
  }
  
  .card .face.face2:before{
    content:'';
    position: absolute;
    top:0;
    left:0;
    width: 50%;
    height: 100%;
    background: rgba(255,255,255, 0.1);
    border-radius: 15px 0 0 15px;
  }
  
  .card .face.face2 h2{
    margin: 0;
    padding: 0;
    font-size: 10em;
    color: #fff;
    transition: 0.5s;
    /* text-shadow: 0 1px 3px rgba(0,0,0, .2); */
  }
  
  .card:hover .face.face2 h2{
    font-size: 2em;
  }
  .social-icons a{
    display:inline-block;
    height:3.5rem;
    width:3.5rem;
    background-color:#09cebd;
    background-image: linear-gradient(#09cebd);
    color:#fff!important;
    border-radius:100%;
    text-align:center;
    font-size:1.5rem;
    line-height:3.5rem;
    margin-right:1rem
  }
  .social-icons a:last-child{margin-right:0}
  .social-icons a:hover{
    background-color:#09cebd;
    background-image: linear-gradient(to bottom right,#09cebd,#259289)
  }
  .project{
    background:#d3f6f3 ;
  }